import Vue from 'vue'
import Router from 'vue-router'
const Login = () => import('../components/Login.vue')
const Guide = () => import('../components/Guide.vue')
const List = () => import('../components/List.vue')
const AreaList = () => import('../components/AreaList.vue')
const AreaDet = () => import('../components/AreaDet.vue')
const EditAddress = () => import('../components/EditAddress.vue')
const EditName = () => import('../components/EditName.vue')
const Loaction = () => import('../components/Loaction.vue')
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/Login',
      meta: { title: '登录' },
      name: 'Login',
      component: Login
    },
    {
      path: '/Guide',
      meta: { title: '土石方计数系统' },
      name: 'Guide',
      component: Guide
    },
    {
      path: '/List',
      meta: { title: '数据统计' },
      name: 'List',
      component: List
    },
    {
      path: '/AreaList',
      meta: { title: '工区管理' },
      name: 'AreaList',
      component: AreaList
    },
    {
      path: '/AreaDet',
      meta: { title: '工区详情' },
      name: 'AreaDet',
      component: AreaDet
    },
    {
      path: '/EditAddress',
      meta: { title: '工区管理编辑' },
      name: 'EditAddress',
      component: EditAddress
    },
    {
      path: '/EditName',
      meta: { title: '工区名称' },
      name: 'EditName',
      component: EditName
    },
    {
      path: '/',
      meta: { title: '工区位置' },
      name: 'Loaction',
      component: Loaction
    }
  ]
})



